<template>
  <main class="card" role="region" aria-label="Секундомер">
    <h1 class="title">
      Регламент:
      <span style="color: red; font-weight: bold">60</span> минут
    </h1>
    <div id="display" class="timer" aria-live="polite" aria-atomic="true">
      {{ formattedTime }}
    </div>
    <div class="controls">
      <v-btn
          class="button"
          :color="isRunning ? 'red-darken-1' : 'green-darken-1'"
          @click="toggle"
          :class="isRunning ? 'stop' : 'start'"
      >
        {{ isRunning ? 'Стоп' : 'Старт' }}
      </v-btn>
      <v-btn @click="reset" class="ghost" title="Сброс">
        Сброс
      </v-btn>
    </div>
  </main>
</template>

<script>
export default {
  name: "StopwatchTimer",
  data() {
    return {
      startTime: null,     // фиксируем время старта
      elapsedSec: 0,       // прошедшее время в секундах
      intervalId: null,    // id таймера
    };
  },
  computed: {
    isRunning() {
      return this.intervalId !== null;
    },
    formattedTime() {
      const pad2 = (n) => String(n).padStart(2, "0");
      const h = Math.floor(this.elapsedSec / 3600);
      const m = Math.floor((this.elapsedSec % 3600) / 60);
      const s = this.elapsedSec % 60;
      return `${pad2(h)}:${pad2(m)}:${pad2(s)}`;
    },
  },
  methods: {
    start() {
      if (this.isRunning) return;
      // фиксируем момент старта, учитываем уже пройденное время
      this.startTime = Date.now() - this.elapsedSec * 1000;
      this.intervalId = setInterval(() => {
        const now = Date.now();
        this.elapsedSec = Math.floor((now - this.startTime) / 1000);
      }, 200); // обновляем чаще, чем раз в секунду для точности
    },
    stop() {
      if (!this.isRunning) return;
      clearInterval(this.intervalId);
      this.intervalId = null;
    },
    toggle() {
      this.isRunning ? this.stop() : this.start();
    },
    reset() {
      this.stop();
      this.elapsedSec = 0;
    },
  },
  beforeUnmount() {
    if (this.intervalId) clearInterval(this.intervalId);
  },
};
</script>

<style scoped>
:root {
  --bg: #0f172a;
  --card: #111827;
  --text: #fff5ee;
  --muted: #9ca3af;
  --accent: #22c55e;
  --danger: #ef4444;
}

.card {
  width: min(270px, 90vw);
  border: 1px solid rgba(255, 255, 255, 0.06);
  backdrop-filter: blur(6px);
  border-radius: 5px;
  padding: 10px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
  color: var(--text);
  background-color: var(--card);
}

.title {
  font-weight: 700;
  font-size: 1.25rem;
}

.timer {
  font-variant-numeric: tabular-nums;
  font-weight: 800;
  letter-spacing: 2px;
  font-size: clamp(30px, 8vw, 45px);
  text-align: center;
  color: var(--text);
  margin-bottom: 10px;
}

.controls {
  display: flex;
  gap: 12px;
  justify-content: center;
}
</style>